  border-radius: 4px;
  padding: 10px 25px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.search-form-container {
  background-color: #1e90ff;
  justify-content: center;
  padding-top: 20px;
  padding-bottom: 20px;
  display: flex;
}

.input {
  justify-content: center;
  align-items: center;
}

